<template>
  <div class="card-content">
    <div class="arrow-icon" @click="clicked" v-on:click="$emit('bclicked')">
      <span>بیشتر</span>
      <span class="icon-left-big a-icon" :class="[clk==1 ? 'selected' : 'blabla']"></span>
    </div>
    <div class="content-text" :class="[clk==1 ? 'selected' : 'blabla']">
      <p>
        جشن نوروز که تا هفت شبانه روز ادامه داشت گاهی به فرمان شاه عباس بر سر پل برگزار می‌شد و مراسم گلریزان
        صورت می‌گرفت، و گل‌های فراوان در راه شاه و همراهان او ریخته می‌شد.
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: "CardContent",
  data() {
    return {
      clk: 0,
    };
  },
  methods: {
    clicked: function () {
      if (this.clk == 0) {
        this.clk = 1;
      } else this.clk = 0;
    },
  },
};
</script>

<style scoped>
.card-content {
  width: auto;
  position: relative;
}

.content-text {
  padding-right: 20px;
  opacity: 75%;
  width: 0px;
  overflow: hidden;
  transition: 0.6s;
}

.content-text p {
  font-size: 1.5rem;
  color: white;
  width: 300px;
  padding: 5rem;
}

.content-text.selected {
  width: 500px;
}

.arrow-icon {
  color: white;
  background-color: #7581b1;
  position: absolute;
  padding: 10px;
  font-size: 2rem;
  border-radius: 40px;
  top: 40%;
  right: -70px;
  display: flex;
  transition: 0.5s;
}

.arrow-icon:hover {
  cursor: pointer;
  background-color: #496bf3;
}

.arrow-icon:active {
  cursor: pointer;
  background-color: #7581b1;
}

.a-icon {
  font-size: 1.8rem;
  transition: 0.6s;
  margin-top: 5px;
}
.a-icon.selected {
  transform: rotate(180deg);
}

@media screen and (max-width: 920px) {
  .content-text p {
    font-size: 1rem;
  }
  .arrow-icon {
    font-size: 1.5rem;
  }
  .a-icon {
    font-size: 1.3rem;
  }
}
@media screen and (max-width: 846px) {
  .arrow-icon {
    font-size: 1rem;
    right: -35px;
  }
  .a-icon {
    font-size: 0.8rem;
  }
}
@media screen and (max-width: 795px) {
  .arrow-icon {
    display: none;
  }
  .card-content {
    width: 100%;
    height: 30%;
  }
  .content-text {
    width: 100%;
    padding: 0;
  }
  .content-text p {
    font-size: 1rem;
    padding: 7px;
    width: auto;
  }
}
</style>