<template>
  <div class="gallery-main">
    <div class="gallery-img">
      <img v-if="choosen.id==1" src="../assets/images/img4.jpg" :alt="choosen.title" />
      <img v-if="choosen.id==2" src="../assets/images/img5.jpg" :alt="choosen.title" />
      <img v-if="choosen.id==3" src="../assets/images/img6.jpg" :alt="choosen.title" />
      <img v-if="choosen.id==4" src="../assets/images/img7.jpg" :alt="choosen.title" />
      <img v-if="choosen.id==5" src="../assets/images/img8.jpg" :alt="choosen.title" />
      <img v-if="choosen.id==6" src="../assets/images/img9.jpg" :alt="choosen.title" />
      <img v-if="choosen.id==7" src="../assets/images/img10.jpg" :alt="choosen.title" />
      <img v-if="choosen.id==8" src="../assets/images/img11.jpg" :alt="choosen.title" />
    </div>
    <div class="img-details">
      <div class="detail-heading">
        <h3>{{choosen.title}}</h3>
        <span class="like-box">
          <div>
            <i class="icon-thumbs-up-alt like-icon" @click="choosen.like++"></i>
            <p>{{choosen.like}}</p>
          </div>
          <div>
            <i class="icon-thumbs-down-alt like-icon" @click="choosen.dislike++"></i>
            <p>{{choosen.dislike}}</p>
          </div>
        </span>
      </div>
      <div class="comment-part">
        <p>کامنت ها</p>
        <div class="comment-box">
          <ul>
            <li v-if="choosen.comments.length==0">نظری برای نمایش وجود ندارد</li>
            <li v-else v-for="comment in choosen.comments" :key="comment.key">{{comment}}</li>
          </ul>
        </div>
        <div class="add-comment">
          <input type="text" placeholder="افزودن نظر" v-model="cmnt" />
          <button @click="addComment">ارسال</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "GalleryMain",
  props: ["choosen"],
  data() {
    return {
      cmnt: "",
    };
  },
  methods: {
    addComment: function () {
      var name = this.cmnt;
      if (name != "") {
        this.choosen.comments.push(name);
      }
      this.cmnt = "";
    },
  },
};
</script>

<style scoped>
.gallery-main {
  display: flex;
  height: 80%;
}

.gallery-img {
  width: 50%;
  height: 100%;
  background-size: cover;
  position: relative;
  overflow: hidden;
}

.gallery-img img {
  width: 90%;
  height: 90%;
  position: absolute;
  top: 5%;
  right: 5%;
  border-radius: 3px;
}

.img-details {
  width: 50%;
  height: 100%;
}

.img-details h3 {
  color: white;
  font-size: 1.5rem;
  margin-top: 1rem;
}

.img-details p {
  color: white;
}

.like-box {
  display: flex;
  margin-top: 0.5rem;
  color: white;
  font-size: 1.5rem;
}

.like-icon {
  transition: 0.4s;
  cursor: pointer;
}

.like-icon:hover {
  color: goldenrod;
  cursor: pointer;
}

.like-icon:active {
  color: rgb(255, 183, 0);
  cursor: pointer;
}

.like-box p {
  font-size: 1.2rem;
  margin: 0 0.8rem;
}
.comment-part {
  height: 100%;
}

.comment-box {
  background-color: white;
  width: 90%;
  height: 52%;
  border-radius: 2px;
  overflow-y: scroll;
}

.comment-box ul {
  list-style-type: none;
}

.comment-box ul li {
  word-break: break-all;
  background-color: lightgray;
  margin: 5px;
}

.add-comment {
  margin-top: 3px;
  width: 90%;
  display: flex;
  flex-wrap: nowrap;
}

.add-comment input {
  width: 85%;
  height: 30px;
  padding-right: 4px;
  border-radius: 10px;
  border: 1px solid whitesmoke;
  font-family: Vazir;
  transition: 0.4s;
}

.add-comment input:hover {
  border: 1px solid #7581b1;
  background-color: whitesmoke;
}

.add-comment button {
  width: 13%;
  height: 34px;
  border-radius: 10px;
  font-family: Vazir;
  margin-right: 3px;
  border: none;
  background-color: #7581b1;
  color: white;
  transition: 0.4s;
}

.add-comment button:hover {
  background-color: #3f55aa;
  cursor: pointer;
}

.add-comment button:active {
  background-color: #4d5575;
}

.add-comment button:focus,
.add-comment input:focus {
  outline: none;
}

@media screen and (max-width: 1000px) {
  .gallery-main {
    flex-direction: column;
  }
  .gallery-img {
    width: 100%;
    height: 50%;
    position: unset;
    display: flex;
    justify-content: center;
  }
  .gallery-img img {
    width: auto;
    height: 100%;
    position: unset;
  }
  .detail-heading {
    display: flex;
    justify-content: center;
  }
  .img-details {
    width: 100%;
    height: 50%;
  }
  .like-box {
    margin: 1rem 1rem 0 0;
  }
  .comment-part {
    height: 100%;
    margin-right: 5px;
  }
  .comment-box {
    height: 30%;
  }
}
</style>